<template>
  <div>
    <!--    面包屑导航-->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>商品添加</el-breadcrumb-item>
    </el-breadcrumb>
<!--    卡片视图-->
    <el-card>
<!--      提示-->
      <el-alert
          title="添加商品信息"
          type="info"
          center
          show-icon>
      </el-alert>
<!--      步骤条-->
      <el-steps :space="200" :active="activeIndex-0" finish-status="success" align-center>
        <el-step title="基本信息"></el-step>
        <el-step title="商品参数"></el-step>
        <el-step title="商品属性"></el-step>
        <el-step title="商品图片"></el-step>
        <el-step title="商品内容"></el-step>
        <el-step title="完成"></el-step>
      </el-steps>
<!--      tab栏-->
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="100px" label-position="top">
        <el-tabs :tab-position="'left'" v-model='activeIndex' :before-leave="beforeTabLeave" @tab-click="tabClick">
          <el-tab-pane label="基本信息" name="0">
            <el-form-item label="商品名称" prop="goods_name">
              <el-input v-model="addForm.goods_name"></el-input>
            </el-form-item>
            <el-form-item label="商品价格" prop="goods_price">
              <el-input v-model="addForm.goods_price" type="number"></el-input>
            </el-form-item>
            <el-form-item label="商品重量" prop="goods_weight">
              <el-input v-model="addForm.goods_weight" type="number"></el-input>
            </el-form-item>
            <el-form-item label="商品数量" prop="goods_number">
              <el-input v-model="addForm.goods_number" type="number"></el-input>
            </el-form-item>
            <el-form-item label="商品分类" prop="goods_cat">
              <el-cascader
                  expand-trigger="hover"
                  v-model="addForm.goods_cat"
                  :options="cateList"
                  :props="cateProps"
                  @change="handleChange"
                  clearable
                  change-on-select
              >
              </el-cascader>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="商品参数" name="1">
            <el-form-item v-for="item in manyTableData" :key="item.attr_id" :label="item.attr_name">
              <el-checkbox-group v-model="item.attr_vals">
                  <el-checkbox v-for="(value,index) in item.attr_vals" :key="index" :label="value" border></el-checkbox>
              </el-checkbox-group>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="商品属性" name="2">
            <el-form-item v-for="item in onlyTableData" :key="item.attr_id" :label="item.attr_name">
              <el-input v-model="item.attr_vals"></el-input>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="商品图片" name="3">
            <el-upload
                :action="uploadUrl"
                :on-preview="handlePreview"
                :on-remove="handleRemove"
                list-type="picture" :headers="headerObj" :on-success="handleSuccess">
              <el-button size="small" type="primary">点击上传</el-button>
              <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
            </el-upload>
          </el-tab-pane>
          <el-tab-pane label="商品内容" name="4">
            <quill-editor v-model="addForm.goods_introduce">
            </quill-editor>
            <el-button type="primary" class="addBtn" @click="addGood">添加商品</el-button>
          </el-tab-pane>
        </el-tabs>
      </el-form>
    </el-card>
<!--    图片预览-->
    <el-dialog
        title="图片预览"
        :visible.sync="previewVisible"
        width="50%"
    >
      <img :src="previewPath" alt="">
    </el-dialog>
  </div>
</template>

<script>
import _ from 'lodash'
export default {
  name: "Add",
  data(){
    return{
      // 步骤条激活下标
      activeIndex:'0',
      addForm:{
        goods_name:'',
        goods_price:0,
        goods_weight:0,
        goods_number:0,
        goods_cat:[],
        pics:[],
        goods_introduce:'',
        attrs:[]
      },
      addFormRules:{
        goods_name:[
          {required: true, message: '请输入商品名称', trigger: 'blur'},
          {min: 3, max: 10, message: '长度在 3 到 10 个字符', trigger: 'blur'}
        ],
        goods_price:[
          {required: true, message: '请输入商品价格', trigger: 'blur'},
        ],
        goods_weight:[
          {required: true, message: '请输入商品重量', trigger: 'blur'},
        ],
        goods_number:[
          {required: true, message: '请输入商品数量', trigger: 'blur'},
        ],
        goods_cat:[
          {required: true, message: '请选择商品分类', trigger: 'blur'},
        ]
      },
      cateList:[],
      cateProps:{
        label:'cat_name',
        value:'cat_id',
        children:'children'
      },
      manyTableData:[],
      onlyTableData:[],
      uploadUrl:'http://118.89.233.180:8888/api/private/v1/upload',
      headerObj:{
        Authorization:window.sessionStorage.getItem('token')
      },
      previewPath:'',
      previewVisible:false
    }
  },
  created() {
    this.getCateList()
  },
  methods:{
    async getCateList(){
      const {data:res}=await this.$http.get('categories')
      if (res.meta.status!==200){
        return this.$message.error('获取商品分类失败!')
      }
      this.cateList=res.data
    },
    handleChange(){
      console.log(this.addForm.goods_cat)
      if (this.addForm.goods_cat.length!==3){
        this.addForm.goods_cat=[]
        return
      }
    },
    beforeTabLeave(activeName,oldActiveName){
      if (oldActiveName==='0' && this.addForm.goods_cat.length!==3){
        this.$message.error('请选择商品分类')
        return false
      }
    },
    async tabClick(){
      if (this.activeIndex==='1'){
        const {data:res}=await this.$http.get(`categories/${this.cateId}/attributes`,{
          params:{
            sel:'many'
          }
        })
        if (res.meta.status!==200){
          return this.$message.error('获取动态参数列表失败')
        }
        res.data.forEach(item=>{
          item.attr_vals=item.attr_vals ? item.attr_vals.split(' ') : []
        })
        this.manyTableData=res.data
        console.log(this.manyTableData)
      }else if(this.activeIndex==='2'){
        const {data:res}=await this.$http.get(`categories/${this.cateId}/attributes`,{
          params:{
            sel:'only'
          }
        })
        if (res.meta.status!==200){
          return this.$message.error('获取静态参数列表失败')
        }
        // res.data.forEach(item=>{
        //   item.attr_vals=item.attr_vals.length===0 ? [] : item.attr_vals.split(' ')
        // })
        this.onlyTableData=res.data
        console.log(this.onlyTableData)
      }
    },
    //处理图片预览效果
    handlePreview(file){
      console.log(file)
      this.previewPath=file.response.data.url
      console.log(this.previewPath)
      this.previewVisible=true
    },
    //移除图片
    handleRemove(file){
      // 获取需移除图片路径
      const filePath=file.response.data.tmp_path
      // 获取移除图片在addForm,pics的下标
      const index=this.addForm.pics.findIndex(item=>item.pic===filePath)
      this.addForm.pics.splice(index,1)
      console.log(this.addForm)
    },
    //图片上传成功
    handleSuccess(response){
    //  拼接得到一个图片信息对象
      const picInfo={
        pics:response.data.tmp_path
      }
      this.addForm.pics.push(picInfo)
      console.log(this.addForm)
    },
    addGood(){
      this.$refs.addFormRef.validate(async valid=>{
        if (!valid){
          return this.$message.error('请填写必要的表单项')
        }
        const form=_.cloneDeep(this.addForm)
        form.goods_cat=form.goods_cat.join(',')
        //处理动态参数
        this.manyTableData.forEach(item=>{
          const newInfo={
            attr_id:item.attr_id,
            attr_value:item.attr_vals.join(' ')
          }
          this.addForm.attrs.push(newInfo)
        })
        //处理静态属性
        this.onlyTableData.forEach(item=>{
          const newInfo={
            attr_id:item.attr_id,
            attr_value:item.attr_vals
          }
          this.addForm.attrs.push(newInfo)
        })
        form.attrs=this.addForm.attrs
        console.log(form)
      //  发起请求添加商品 商品名称必须唯一
        const {data:res}=await this.$http.post('goods',form)
        if (res.meta.status!==201){
          return this.$message.error('添加商品失败')
        }
        this.$message.success('添加商品成功')
        this.$router.push('/goods')
      })
    }
  },
  computed:{
    cateId(){
      if (this.addForm.goods_cat.length===3){
        return this.addForm.goods_cat[2]
      }
      return  null
    }
  }
}
</script>

<style lang="less" scoped>
  .el-steps{
    margin:15px 0;
  }
  .el-step__title{
    font-size: 13px;
  }
  .el-checkbox{
    margin: 0 5px 0 0;
  }
  .addBtn{
    margin: 10px 0;
  }
</style>